import {

  configApiRef,

  useApi,
} from '@backstage/core-plugin-api';
import { UserIdentity } from './UserIdentity';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import React, { useState } from 'react';
import { useMountEffect } from '@react-hookz/web';
import { Progress } from '../../components/Progress';
import { Content } from '../Content/Content';
import { ContentHeader } from '../ContentHeader/ContentHeader';
import { Header } from '../Header';
import { InfoCard } from '../InfoCard';
import { Page } from '../Page';
import { getSignInProviders, useSignInProviders } from './providers';
import { GridItem, useStyles } from './styles';















export const MultiSignInPage = ({
  onSignInSuccess,
  providers = [],
  title,
  align = 'left',
}) => {
  const configApi = useApi(configApiRef);
  const classes = useStyles();

  const signInProviders = getSignInProviders(providers);
  const [loading, providerElements] = useSignInProviders(
    signInProviders,
    onSignInSuccess,
  );

  if (loading) {
    return React.createElement(Progress, null );
  }

  return (
    React.createElement(Page, { themeId: "home",}
      , React.createElement(Header, { title: configApi.getString('app.title'),} )
      , React.createElement(Content, null
        , title && React.createElement(ContentHeader, { title: title, textAlign: align,} )
        , React.createElement(Grid, {
          container: true,
          justifyContent: align === 'center' ? align : 'flex-start',
          spacing: 2,
          component: "ul",
          classes: classes,}
        
          , providerElements
        )
      )
    )
  );
};

export const SingleSignInPage = ({
  provider,
  auto,
  onSignInSuccess,
}) => {
  const classes = useStyles();
  const authApi = useApi(provider.apiRef);
  const configApi = useApi(configApiRef);

  const [error, setError] = useState();

  // The SignIn component takes some time to decide whether the user is logged-in or not.
  // showLoginPage is used to prevent a glitch-like experience where the sign-in page is
  // displayed for a split second when the user is already logged-in.
  const [showLoginPage, setShowLoginPage] = useState(false);

  
  const login = async ({ checkExisting, showPopup }) => {
    try {
      let identityResponse;
      if (checkExisting) {
        // Do an initial check if any logged-in session exists
        identityResponse = await authApi.getBackstageIdentity({
          optional: true,
        });
      }

      // If no session exists, show the sign-in page
      if (!identityResponse && (showPopup || auto)) {
        // Unless auto is set to true, this step should not happen.
        // When user intentionally clicks the Sign In button, autoShowPopup is set to true
        setShowLoginPage(true);
        identityResponse = await authApi.getBackstageIdentity({
          instantPopup: true,
        });
        if (!identityResponse) {
          throw new Error(
            `The ${provider.title} provider is not configured to support sign-in`,
          );
        }
      }

      if (!identityResponse) {
        setShowLoginPage(true);
        return;
      }

      const profile = await authApi.getProfile();
      onSignInSuccess(
        UserIdentity.create({
          identity: identityResponse.identity,
          authApi,
          profile,
        }),
      );
    } catch (err) {
      // User closed the sign-in modal
      setError(err);
      setShowLoginPage(true);
    }
  };

  useMountEffect(() => login({ checkExisting: true }));

  return showLoginPage ? (
    React.createElement(Page, { themeId: "home",}
      , React.createElement(Header, { title: configApi.getString('app.title'),} )
      , React.createElement(Content, null
        , React.createElement(Grid, {
          container: true,
          justifyContent: "center",
          spacing: 2,
          component: "ul",
          classes: classes,}
        
          , React.createElement(GridItem, null
            , React.createElement(InfoCard, {
              variant: "fullHeight",
              title: provider.title,
              actions: 
                React.createElement(Button, {
                  color: "primary",
                  variant: "outlined",
                  onClick: () => {
                    login({ showPopup: true });
                  },}
                , "Sign In"

                )
              ,}
            
              , React.createElement(Typography, { variant: "body1",}, provider.message)
              , error && error.name !== 'PopupRejectedError' && (
                React.createElement(Typography, { variant: "body1", color: "error",}
                  , error.message
                )
              )
            )
          )
        )
      )
    )
  ) : (
    React.createElement(Progress, null )
  );
};

export function SignInPage(props) {
  if ('provider' in props) {
    return React.createElement(SingleSignInPage, { ...props,} );
  }

  return React.createElement(MultiSignInPage, { ...props,} );
}
