

















import {
  scmIntegrationsApiRef,
  ScmIntegrationsApi,
} from '@backstage/integration-react';
import {

  configApiRef,
  createApiFactory,

  discoveryApiRef,

  identityApiRef,
} from '@backstage/core-plugin-api';
import {


  techdocsApiRef,

  techdocsStorageApiRef,
} from '@backstage/plugin-techdocs-react';

// TODO: Export type from plugin-techdocs and import this here
// import { ParsedEntityId } from '@backstage/plugin-techdocs'

/**
 * Note: Override TechDocs API to use local mkdocs server instead of techdocs-backend.
 */

class TechDocsDevStorageApi  {
   configApi;
   discoveryApi;
   identityApi;

  constructor({
    configApi,
    discoveryApi,
    identityApi,
  }



) {
    this.configApi = configApi;
    this.discoveryApi = discoveryApi;
    this.identityApi = identityApi;
  }

  async getApiOrigin() {
    return await this.discoveryApi.getBaseUrl('techdocs');
  }

  async getStorageUrl() {
    return `${await this.discoveryApi.getBaseUrl('techdocs')}/static/docs`;
  }

  async getBuilder() {
    return this.configApi.getString('techdocs.builder');
  }

  async getEntityDocs(_entityId, path) {
    const apiOrigin = await this.getApiOrigin();
    // Irrespective of the entity, use mkdocs server to find the file for the path.
    const url = `${apiOrigin}/${path}`;

    const request = await fetch(
      `${url.endsWith('/') ? url : `${url}/`}index.html`,
    );

    if (request.status === 404) {
      throw new Error('Page not found');
    }

    return request.text();
  }

  async syncEntityDocs(_) {
    // this is just stub of this function as we don't need to check if docs are up to date,
    // we always want to retrigger a new build
    return 'cached';
  }

  // Used by transformer to modify the request to assets (CSS, Image) from inside the HTML.
  async getBaseUrl(
    oldBaseUrl,
    _entityId,
    path,
  ) {
    const apiOrigin = await this.getApiOrigin();
    return new URL(oldBaseUrl, `${apiOrigin}/${path}`).toString();
  }
}

class TechDocsDevApi  {
   configApi;
   discoveryApi;
   identityApi;

  constructor({
    configApi,
    discoveryApi,
    identityApi,
  }



) {
    this.configApi = configApi;
    this.discoveryApi = discoveryApi;
    this.identityApi = identityApi;
  }

  async getApiOrigin() {
    return await this.discoveryApi.getBaseUrl('techdocs');
  }

  async getEntityMetadata(_entityId) {
    return {
      apiVersion: 'backstage.io/v1alpha1',
      kind: 'Component',
      metadata: {
        name: 'local',
      },
      spec: {
        owner: 'test',
        lifecycle: 'experimental',
      },
    };
  }

  async getTechDocsMetadata(_entityId) {
    return {
      site_name: 'Live preview environment',
      site_description: '',
    };
  }
}

export const apis = [
  createApiFactory({
    api: techdocsStorageApiRef,
    deps: {
      configApi: configApiRef,
      discoveryApi: discoveryApiRef,
      identityApi: identityApiRef,
    },
    factory: ({ configApi, discoveryApi, identityApi }) =>
      new TechDocsDevStorageApi({
        configApi,
        discoveryApi,
        identityApi,
      }),
  }),
  createApiFactory({
    api: techdocsApiRef,
    deps: {
      configApi: configApiRef,
      discoveryApi: discoveryApiRef,
      identityApi: identityApiRef,
    },
    factory: ({ configApi, discoveryApi, identityApi }) =>
      new TechDocsDevApi({
        configApi,
        discoveryApi,
        identityApi,
      }),
  }),
  createApiFactory({
    api: scmIntegrationsApiRef,
    deps: { configApi: configApiRef },
    factory: ({ configApi }) => ScmIntegrationsApi.fromConfig(configApi),
  }),
];
