import React from 'react';
import { Link, SubvalueCell, } from '@backstage/core-components';
import { EntityRefLinks } from '@backstage/plugin-catalog-react';



function customTitle(entity) {
  return entity.metadata.title || entity.metadata.name;
}

/**
 * Not directly exported, but through DocsTable.columns and EntityListDocsTable.columns
 *
 * @public
 */
export const columnFactories = {
  createNameColumn() {
    return {
      title: 'Document',
      field: 'entity.metadata.name',
      highlight: true,
      render: (row) => (
        React.createElement(SubvalueCell, {
          value: 
            React.createElement(Link, { to: row.resolved.docsUrl,}, customTitle(row.entity))
          ,
          subvalue: row.entity.metadata.description,}
        )
      ),
    };
  },
  createOwnerColumn() {
    return {
      title: 'Owner',
      field: 'resolved.ownedByRelationsTitle',
      render: ({ resolved }) => (
        React.createElement(EntityRefLinks, {
          entityRefs: resolved.ownedByRelations,
          defaultKind: "group",}
        )
      ),
    };
  },
  createTypeColumn() {
    return {
      title: 'Type',
      field: 'entity.spec.type',
    };
  },
};
