import React from 'react';
import classnames from 'classnames';
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import LinkIcon from '@material-ui/icons/Link';
import { Link as RouterLink } from '../Link';



















const useIconStyles = makeStyles(
  theme => ({
    link: {
      display: 'grid',
      justifyItems: 'center',
      gridGap: 4,
      textAlign: 'center',
    },
    disabled: {
      color: 'gray',
      cursor: 'default',
    },
    primary: {
      color: theme.palette.primary.main,
    },
    secondary: {
      color: theme.palette.secondary.main,
    },
    label: {
      fontSize: '0.7rem',
      textTransform: 'uppercase',
      fontWeight: 600,
      letterSpacing: 1.2,
    },
  }),
  { name: 'BackstageIconLinkVertical' },
);

/** @public */
export function IconLinkVertical({
  color = 'primary',
  disabled = false,
  href = '#',
  icon = React.createElement(LinkIcon, null ),
  label,
  onClick,
  title,
}) {
  const classes = useIconStyles();

  if (disabled) {
    return (
      React.createElement(Link, {
        title: title,
        className: classnames(classes.link, classes.disabled),
        underline: "none",}
      
        , icon
        , React.createElement('span', { className: classes.label,}, label)
      )
    );
  }

  return (
    React.createElement(Link, {
      title: title,
      className: classnames(classes.link, classes[color]),
      to: href,
      component: RouterLink,
      onClick: onClick,}
    
      , icon
      , React.createElement('span', { className: classes.label,}, label)
    )
  );
}
