import { rootDocsRouteRef } from '../../../routes';
import { toLowerMaybe } from '../../../helpers';

import { useApi, useRouteRef, configApiRef } from '@backstage/core-plugin-api';
import {
  Button,
  ItemCardGrid,
  ItemCardHeader,
} from '@backstage/core-components';
import { Card, CardActions, CardContent, CardMedia } from '@material-ui/core';
import React from 'react';

/**
 * Props for {@link DocsCardGrid}
 *
 * @public
 */




/**
 * Component which accepts a list of entities and renders a item card for each entity
 *
 * @public
 */
export const DocsCardGrid = (props) => {
  const { entities } = props;
  const getRouteToReaderPageFor = useRouteRef(rootDocsRouteRef);
  const config = useApi(configApiRef);
  if (!entities) return null;
  return (
    React.createElement(ItemCardGrid, { 'data-testid': "docs-explore",}
      , !entities?.length
        ? null
        : entities.map((entity, index) => (
            React.createElement(Card, { key: index,}
              , React.createElement(CardMedia, null
                , React.createElement(ItemCardHeader, {
                  title: entity.metadata.title ?? entity.metadata.name,}
                )
              )
              , React.createElement(CardContent, null, entity.metadata.description)
              , React.createElement(CardActions, null
                , React.createElement(Button, {
                  to: getRouteToReaderPageFor({
                    namespace: toLowerMaybe(
                      entity.metadata.namespace ?? 'default',
                      config,
                    ),
                    kind: toLowerMaybe(entity.kind, config),
                    name: toLowerMaybe(entity.metadata.name, config),
                  }),
                  color: "primary",
                  'data-testid': "read_docs",}
                , "Read Docs"

                )
              )
            )
          ))
    )
  );
};
