
















import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Circle } from 'rc-progress';
import React, { useEffect, useState } from 'react';

/** @public */







const useStyles = makeStyles(
  theme => ({
    root: {
      position: 'relative',
      lineHeight: 0,
    },
    overlay: {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -60%)',
      fontSize: 45,
      fontWeight: 'bold',
      color: theme.palette.textContrast,
    },
    description: {
      fontSize: '100%',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      position: 'absolute',
      wordBreak: 'break-all',
      display: 'inline-block',
    },
    circle: {
      width: '80%',
      transform: 'translate(10%, 0)',
    },
    colorUnknown: {},
  }),
  { name: 'BackstageGauge' },
);

/** @public */





















const defaultGaugeProps = {
  fractional: true,
  inverse: false,
  unit: '%',
  max: 100,
};

export const getProgressColor = ({
  palette,
  value,
  inverse,
  max,
}) => {
  if (isNaN(value)) {
    return '#ddd';
  }

  const actualMax = max ? max : defaultGaugeProps.max;
  const actualValue = inverse ? actualMax - value : value;

  if (actualValue < actualMax / 3) {
    return palette.status.error;
  } else if (actualValue < actualMax * (2 / 3)) {
    return palette.status.warning;
  }

  return palette.status.ok;
};

/**
 * Circular Progress Bar
 *
 * @public
 *
 */

export function Gauge(props) {
  const [hoverRef, setHoverRef] = useState(null);
  const { getColor = getProgressColor } = props;
  const classes = useStyles(props);
  const { palette } = useTheme();
  const { value, fractional, inverse, unit, max, description } = {
    ...defaultGaugeProps,
    ...props,
  };

  const asPercentage = fractional ? Math.round(value * max) : value;
  const asActual = max !== 100 ? Math.round(value) : asPercentage;

  const [isHovering, setIsHovering] = useState(false);

  useEffect(() => {
    const node = hoverRef;
    const handleMouseOver = () => setIsHovering(true);
    const handleMouseOut = () => setIsHovering(false);
    if (node && description) {
      node.addEventListener('mouseenter', handleMouseOver);
      node.addEventListener('mouseleave', handleMouseOut);

      return () => {
        node.removeEventListener('mouseenter', handleMouseOver);
        node.removeEventListener('mouseleave', handleMouseOut);
      };
    }
    return () => {
      setIsHovering(false);
    };
  }, [description, hoverRef]);

  return (
    React.createElement('div', { ref: setHoverRef, className: classes.root,}
      , React.createElement(Circle, {
        strokeLinecap: "butt",
        percent: asPercentage,
        strokeWidth: 12,
        trailWidth: 12,
        strokeColor: getColor({ palette, value: asActual, inverse, max }),
        className: classes.circle,}
      )
      , description && isHovering ? (
        React.createElement('div', { className: classes.description,}, description)
      ) : (
        React.createElement('div', { className: classes.overlay,}
          , isNaN(value) ? 'N/A' : `${asActual}${unit}`
        )
      )
    )
  );
}
