import {
  Divider,
  IconButton,
  ListItemIcon,
  ListItemText,
  MenuItem,
  MenuList,
  Popover,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import CancelIcon from '@material-ui/icons/Cancel';
import BugReportIcon from '@material-ui/icons/BugReport';
import MoreVert from '@material-ui/icons/MoreVert';
import React, { useState } from 'react';

import { useEntityPermission } from '@backstage/plugin-catalog-react';
import { catalogEntityDeletePermission } from '@backstage/plugin-catalog-common';

// TODO(freben): It should probably instead be the case that Header sets the theme text color to white inside itself unconditionally instead
const useStyles = makeStyles({
  button: {
    color: 'white',
  },
});

// NOTE(freben): Intentionally not exported at this point, since it's part of
// the unstable extra context menu items concept below


















export function EntityContextMenu(props) {
  const {
    UNSTABLE_extraContextMenuItems,
    UNSTABLE_contextMenuOptions,
    onUnregisterEntity,
    onInspectEntity,
  } = props;
  const [anchorEl, setAnchorEl] = useState();
  const classes = useStyles();
  const unregisterPermission = useEntityPermission(
    catalogEntityDeletePermission,
  );

  const onOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const onClose = () => {
    setAnchorEl(undefined);
  };

  const extraItems = UNSTABLE_extraContextMenuItems && [
    ...UNSTABLE_extraContextMenuItems.map(item => (
      React.createElement(MenuItem, {
        key: item.title,
        onClick: () => {
          onClose();
          item.onClick();
        },}
      
        , React.createElement(ListItemIcon, null
          , React.createElement(item.Icon, { fontSize: "small",} )
        )
        , React.createElement(ListItemText, { primary: item.title,} )
      )
    )),
    React.createElement(Divider, { key: "the divider is here!"   ,} ),
  ];

  const disableUnregister =
    (!unregisterPermission.allowed ||
      UNSTABLE_contextMenuOptions?.disableUnregister) ??
    false;

  return (
    React.createElement(React.Fragment, null
      , React.createElement(IconButton, {
        'aria-label': "more",
        'aria-controls': "long-menu",
        'aria-haspopup': "true",
        'aria-expanded': !!anchorEl,
        role: "button",
        onClick: onOpen,
        'data-testid': "menu-button",
        className: classes.button,
        id: "long-menu",}
      
        , React.createElement(MoreVert, null )
      )
      , React.createElement(Popover, {
        open: Boolean(anchorEl),
        onClose: onClose,
        anchorEl: anchorEl,
        anchorOrigin: { vertical: 'bottom', horizontal: 'right' },
        transformOrigin: { vertical: 'top', horizontal: 'right' },
        'aria-labelledby': "long-menu",}
      
        , React.createElement(MenuList, null
          , extraItems
          , React.createElement(MenuItem, {
            onClick: () => {
              onClose();
              onUnregisterEntity();
            },
            disabled: disableUnregister,}
          
            , React.createElement(ListItemIcon, null
              , React.createElement(CancelIcon, { fontSize: "small",} )
            )
            , React.createElement(ListItemText, { primary: "Unregister entity" ,} )
          )
          , React.createElement(MenuItem, {
            onClick: () => {
              onClose();
              onInspectEntity();
            },}
          
            , React.createElement(ListItemIcon, null
              , React.createElement(BugReportIcon, { fontSize: "small",} )
            )
            , React.createElement(ListItemText, { primary: "Inspect entity" ,} )
          )
        )
      )
    )
  );
}
