import React, { Fragment, } from 'react';
import {
  withStyles,
  createStyles,


} from '@material-ui/core/styles';
import startCase from 'lodash/startCase';

import {
  MetadataTable,
  MetadataTableItem,
  MetadataList,
  MetadataListItem,
} from './MetadataTable';



const listStyle = createStyles({
  root: {
    margin: '0 0',
    listStyleType: 'none',
  },
});



const nestedListStyle = (theme) =>
  createStyles({
    root: {
      ...listStyle.root,
      paddingLeft: theme.spacing(1),
    },
  });




// Sub Components
const StyledList = withStyles(listStyle, {
  name: 'BackstageStructuredMetadataTableList',
})(({ classes, children }) => (
  React.createElement(MetadataList, { classes: classes,}, children)
));
const StyledNestedList = withStyles(nestedListStyle, {
  name: 'BackstageStructuredMetadataTableNestedList',
})(({ classes, children }) => (
  React.createElement(MetadataList, { classes: classes,}, children)
));

function renderList(list, nested) {
  const values = list.map((item, index) => (
    React.createElement(MetadataListItem, { key: index,}, toValue(item))
  ));
  return nested ? (
    React.createElement(StyledNestedList, null, values)
  ) : (
    React.createElement(StyledList, null, values)
  );
}

function renderMap(
  map,
  nested,
  options,
) {
  const values = Object.keys(map).map(key => {
    const value = toValue(map[key], true);
    const fmtKey =
      options && options.titleFormat
        ? options.titleFormat(key)
        : startCase(key);
    return (
      React.createElement(MetadataListItem, { key: key,}
        , `${fmtKey}: `
        , value
      )
    );
  });

  return nested ? (
    React.createElement(StyledNestedList, null, values)
  ) : (
    React.createElement(StyledList, null, values)
  );
}

function toValue(
  value,
  options,
  nested,
) {
  if (React.isValidElement(value)) {
    return React.createElement(Fragment, null, value);
  }

  if (typeof value === 'object' && !Array.isArray(value)) {
    return renderMap(value, options, nested);
  }

  if (Array.isArray(value)) {
    return renderList(value, nested);
  }

  if (typeof value === 'boolean') {
    return React.createElement(Fragment, null, value ? '' : '');
  }

  return React.createElement(Fragment, null, value);
}
const ItemValue = ({ value, options }) => (
  React.createElement(Fragment, null, toValue(value, options))
);

const TableItem = ({
  title,
  value,
  options,
}



) => {
  return (
    React.createElement(MetadataTableItem, {
      title: 
        options && options.titleFormat
          ? options.titleFormat(title)
          : startCase(title)
      ,}
    
      , React.createElement(ItemValue, { value: value, options: options,} )
    )
  );
};

function mapToItems(info, options) {
  return Object.keys(info).map(key => (
    React.createElement(TableItem, { key: key, title: key, value: info[key], options: options,} )
  ));
}







export function StructuredMetadataTable(props) {
  const { metadata, dense = true, options } = props;
  const metadataItems = mapToItems(metadata, options || {});
  return React.createElement(MetadataTable, { dense: dense,}, metadataItems);
}
