import React from 'react';
import {
  humanizeEntityRef,
  EntityRefLink,
  EntityRefLinks,
} from '@backstage/plugin-catalog-react';
import { Chip } from '@material-ui/core';

import { OverflowTooltip, } from '@backstage/core-components';


// The columnFactories symbol is not directly exported, but through the
// CatalogTable.columns field.
/** @public */
export const columnFactories = Object.freeze({
  createNameColumn(options

) {
    function formatContent(entity) {
      return (
        entity.metadata?.title ||
        humanizeEntityRef(entity, {
          defaultKind: options?.defaultKind,
        })
      );
    }

    return {
      title: 'Name',
      field: 'resolved.name',
      highlight: true,
      customSort({ entity: entity1 }, { entity: entity2 }) {
        // TODO: We could implement this more efficiently by comparing field by field.
        // This has similar issues as above.
        return formatContent(entity1).localeCompare(formatContent(entity2));
      },
      render: ({ entity }) => (
        React.createElement(EntityRefLink, {
          entityRef: entity,
          defaultKind: options?.defaultKind || 'Component',
          title: entity.metadata?.title,}
        )
      ),
    };
  },
  createSystemColumn() {
    return {
      title: 'System',
      field: 'resolved.partOfSystemRelationTitle',
      render: ({ resolved }) => (
        React.createElement(EntityRefLinks, {
          entityRefs: resolved.partOfSystemRelations,
          defaultKind: "system",}
        )
      ),
    };
  },
  createOwnerColumn() {
    return {
      title: 'Owner',
      field: 'resolved.ownedByRelationsTitle',
      render: ({ resolved }) => (
        React.createElement(EntityRefLinks, {
          entityRefs: resolved.ownedByRelations,
          defaultKind: "group",}
        )
      ),
    };
  },
  createSpecTypeColumn() {
    return {
      title: 'Type',
      field: 'entity.spec.type',
      hidden: true,
    };
  },
  createSpecLifecycleColumn() {
    return {
      title: 'Lifecycle',
      field: 'entity.spec.lifecycle',
    };
  },
  createMetadataDescriptionColumn() {
    return {
      title: 'Description',
      field: 'entity.metadata.description',
      render: ({ entity }) => (
        React.createElement(OverflowTooltip, {
          text: entity.metadata.description,
          placement: "bottom-start",}
        )
      ),
      width: 'auto',
    };
  },
  createTagsColumn() {
    return {
      title: 'Tags',
      field: 'entity.metadata.tags',
      cellStyle: {
        padding: '0px 16px 0px 20px',
      },
      render: ({ entity }) => (
        React.createElement(React.Fragment, null
          , entity.metadata.tags &&
            entity.metadata.tags.map(t => (
              React.createElement(Chip, {
                key: t,
                label: t,
                size: "small",
                variant: "outlined",
                style: { marginBottom: '0px' },}
              )
            ))
        )
      ),
    };
  },
});
