import { RELATION_OWNED_BY } from '@backstage/catalog-model';
import {
  Box,
  Checkbox,
  FormControlLabel,
  makeStyles,
  TextField,
  Typography,
} from '@material-ui/core';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Autocomplete } from '@material-ui/lab';
import React, { useEffect, useMemo, useState } from 'react';
import { useEntityList } from '../../hooks/useEntityListProvider';
import { EntityOwnerFilter } from '../../filters';
import { getEntityRelations } from '../../utils';
import { humanizeEntityRef } from '../EntityRefLink';

/** @public */


const useStyles = makeStyles(
  {
    input: {},
  },
  {
    name: 'CatalogReactEntityOwnerPicker',
  },
);

const icon = React.createElement(CheckBoxOutlineBlankIcon, { fontSize: "small",} );
const checkedIcon = React.createElement(CheckBoxIcon, { fontSize: "small",} );

/** @public */
export const EntityOwnerPicker = () => {
  const classes = useStyles();
  const {
    updateFilters,
    backendEntities,
    filters,
    queryParameters: { owners: ownersParameter },
  } = useEntityList();

  const queryParamOwners = useMemo(
    () => [ownersParameter].flat().filter(Boolean) ,
    [ownersParameter],
  );

  const [selectedOwners, setSelectedOwners] = useState(
    queryParamOwners.length ? queryParamOwners : filters.owners?.values ?? [],
  );

  // Set selected owners on query parameter updates; this happens at initial page load and from
  // external updates to the page location.
  useEffect(() => {
    if (queryParamOwners.length) {
      setSelectedOwners(queryParamOwners);
    }
  }, [queryParamOwners]);

  useEffect(() => {
    updateFilters({
      owners: selectedOwners.length
        ? new EntityOwnerFilter(selectedOwners)
        : undefined,
    });
  }, [selectedOwners, updateFilters]);

  const availableOwners = useMemo(
    () =>
      [
        ...new Set(
          backendEntities
            .flatMap((e) =>
              getEntityRelations(e, RELATION_OWNED_BY).map(o =>
                humanizeEntityRef(o, { defaultKind: 'group' }),
              ),
            )
            .filter(Boolean) ,
        ),
      ].sort(),
    [backendEntities],
  );

  if (!availableOwners.length) return null;

  return (
    React.createElement(Box, { pb: 1, pt: 1,}
      , React.createElement(Typography, { variant: "button", component: "label",}, "Owner"

        , React.createElement(Autocomplete, {
          multiple: true,
          options: availableOwners,
          value: selectedOwners,
          onChange: (_, value) => setSelectedOwners(value),
          renderOption: (option, { selected }) => (
            React.createElement(FormControlLabel, {
              control: 
                React.createElement(Checkbox, {
                  icon: icon,
                  checkedIcon: checkedIcon,
                  checked: selected,}
                )
              ,
              label: option,}
            )
          ),
          size: "small",
          popupIcon: React.createElement(ExpandMoreIcon, { 'data-testid': "owner-picker-expand",} ),
          renderInput: params => (
            React.createElement(TextField, {
              ...params,
              className: classes.input,
              variant: "outlined",}
            )
          ),}
        )
      )
    )
  );
};
