import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardHeader, { } from '@material-ui/core/CardHeader';
import Divider from '@material-ui/core/Divider';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import React, { } from 'react';
import { BottomLink, } from '../BottomLink';
import { ErrorBoundary, } from '../ErrorBoundary';

/** @public */









const useStyles = makeStyles(
  theme => ({
    noPadding: {
      padding: 0,
      '&:last-child': {
        paddingBottom: 0,
      },
    },
    header: {
      padding: theme.spacing(2, 2, 2, 2.5),
    },
    headerTitle: {
      fontWeight: 700,
    },
    headerSubheader: {
      paddingTop: theme.spacing(1),
    },
    headerAvatar: {},
    headerAction: {},
    headerContent: {},
    subheader: {
      display: 'flex',
    },
  }),
  { name: 'BackstageInfoCard' },
);

/** @public */


const CardActionsTopRight = withStyles(
  theme => ({
    root: {
      display: 'inline-block',
      padding: theme.spacing(8, 8, 0, 0),
      float: 'right',
    },
  }),
  { name: 'BackstageInfoCardCardActionsTopRight' },
)(CardActions);

const VARIANT_STYLES = {
  card: {
    flex: {
      display: 'flex',
      flexDirection: 'column',
    },
    fullHeight: {
      display: 'flex',
      flexDirection: 'column',
      height: '100%',
    },
    gridItem: {
      display: 'flex',
      flexDirection: 'column',
      height: 'calc(100% - 10px)', // for pages without content header
      marginBottom: '10px',
    },
  },
  cardContent: {
    fullHeight: {
      flex: 1,
    },
    gridItem: {
      flex: 1,
    },
  },
};

/** @public */











































/**
 * Material-ui card with header , content and actions footer
 *
 * @public
 *
 */
export function InfoCard(props) {
  const {
    title,
    subheader,
    divider = true,
    deepLink,
    slackChannel,
    errorBoundaryProps,
    variant,
    children,
    headerStyle,
    headerProps,
    icon,
    action,
    actionsClassName,
    actions,
    cardClassName,
    actionsTopRight,
    className,
    noPadding,
    titleTypographyProps,
  } = props;
  const classes = useStyles();
  /**
   * If variant is specified, we build up styles for that particular variant for both
   * the Card and the CardContent (since these need to be synced)
   */
  let calculatedStyle = {};
  let calculatedCardStyle = {};
  if (variant) {
    const variants = variant.split(/[\s]+/g);
    variants.forEach(name => {
      calculatedStyle = {
        ...calculatedStyle,
        ...VARIANT_STYLES.card[name ],
      };
      calculatedCardStyle = {
        ...calculatedCardStyle,
        ...VARIANT_STYLES.cardContent[
          name 
        ],
      };
    });
  }

  const cardSubTitle = () => {
    return (
      React.createElement('div', { className: classes.headerSubheader,}
        , subheader && React.createElement('div', { className: classes.subheader,}, subheader)
        , icon
      )
    );
  };

  const errProps =
    errorBoundaryProps || (slackChannel ? { slackChannel } : {});

  return (
    React.createElement(Card, { style: calculatedStyle, className: className,}
      , React.createElement(ErrorBoundary, { ...errProps,}
        , title && (
          React.createElement(CardHeader, {
            classes: {
              root: classes.header,
              title: classes.headerTitle,
              subheader: classes.headerSubheader,
              avatar: classes.headerAvatar,
              action: classes.headerAction,
              content: classes.headerContent,
            },
            title: title,
            subheader: cardSubTitle(),
            action: action,
            style: { ...headerStyle },
            titleTypographyProps: titleTypographyProps,
            ...headerProps,}
          )
        )
        , actionsTopRight && (
          React.createElement(CardActionsTopRight, null, actionsTopRight)
        )
        , divider && React.createElement(Divider, null )
        , React.createElement(CardContent, {
          className: classNames(cardClassName, {
            [classes.noPadding]: noPadding,
          }),
          style: calculatedCardStyle,}
        
          , children
        )
        , actions && (
          React.createElement(CardActions, { className: actionsClassName,}, actions)
        )
        , deepLink && React.createElement(BottomLink, { ...deepLink,} )
      )
    )
  );
}
