






















/**
 * Utility function for creating a valid {@link ResourcePermission}, inferring
 * the appropriate type and resource type parameter.
 *
 * @public
 */














export function createPermission({
  name,
  attributes,
  resourceType,
}



) {
  if (resourceType) {
    return {
      type: 'resource',
      name,
      attributes,
      resourceType,
    };
  }

  return {
    type: 'basic',
    name,
    attributes,
  };
}
