
















import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import React from 'react';
import { Link as RouterLink, } from 'react-router-dom';
import AddCircleOutline from '@material-ui/icons/AddCircleOutline';

/**
 * Properties for {@link CreateButton}
 *
 * @public
 */




/**
 * Responsive Button giving consistent UX for creation of different things
 *
 * @public
 */
export function CreateButton(props) {
  const { title, to } = props;
  const isXSScreen = useMediaQuery(theme =>
    theme.breakpoints.down('xs'),
  );

  if (!to) {
    return null;
  }

  return isXSScreen ? (
    React.createElement(IconButton, {
      component: RouterLink,
      color: "primary",
      title: title,
      size: "small",
      to: to,}
    
      , React.createElement(AddCircleOutline, null )
    )
  ) : (
    React.createElement(Button, { component: RouterLink, variant: "contained", color: "primary", to: to,}
      , title
    )
  );
}
