
















import { createApiRef } from '@backstage/core-plugin-api';

/**
 * @public
 */
export const searchApiRef = createApiRef({
  id: 'plugin.search.queryservice',
});

/**
 * @public
 */




/**
 * @public
 *
 * Search Api Mock that can be used in tests and storybooks
 */
export class MockSearchApi  {
  constructor( mockedResults) {;this.mockedResults = mockedResults;}

  query() {
    return Promise.resolve(this.mockedResults || { results: [] });
  }
}
