
















import { catalogApiRef } from '@backstage/plugin-catalog-react';
import { Button, Dialog, DialogActions, DialogTitle } from '@material-ui/core';
import React, { useState } from 'react';
import { alertApiRef, useApi } from '@backstage/core-plugin-api';
import { assertError } from '@backstage/errors';








export function DeleteEntityDialog(props) {
  const { open, onClose, onConfirm, entity } = props;
  const [busy, setBusy] = useState(false);
  const catalogApi = useApi(catalogApiRef);
  const alertApi = useApi(alertApiRef);

  const onDelete = async () => {
    setBusy(true);
    try {
      const uid = entity.metadata.uid;
      await catalogApi.removeEntityByUid(uid);
      onConfirm();
    } catch (err) {
      assertError(err);
      alertApi.post({ message: err.message });
    } finally {
      setBusy(false);
    }
  };

  return (
    React.createElement(Dialog, { open: open, onClose: onClose,}
      , React.createElement(DialogTitle, { id: "responsive-dialog-title",}, "Are you sure you want to delete this entity?"

      )
      , React.createElement(DialogActions, null
        , React.createElement(Button, {
          variant: "contained",
          color: "secondary",
          disabled: busy,
          onClick: onDelete,}
        , "Delete"

        )
        , React.createElement(Button, { onClick: onClose, color: "primary",}, "Cancel"

        )
      )
    )
  );
}
