import React, { Component, } from 'react';
import { Button } from '../../components/Button';
import { ErrorPanel } from '../../components/ErrorPanel';

















const SlackLink = (props) => {
  const { slackChannel } = props;

  if (!slackChannel) {
    return null;
  } else if (typeof slackChannel === 'string') {
    return React.createElement(React.Fragment, null, "Please contact "  , slackChannel, " for help."  );
  } else if (!slackChannel.href) {
    return React.createElement(React.Fragment, null, "Please contact "  , slackChannel.name, " for help."  );
  }

  return (
    React.createElement(Button, { to: slackChannel.href, variant: "contained",}
      , slackChannel.name
    )
  );
};

/** @public */
export const ErrorBoundary


 = class ErrorBoundary extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: undefined,
      errorInfo: undefined,
    };
  }

  componentDidCatch(error, errorInfo) {
    // eslint-disable-next-line no-console
    console.error(`ErrorBoundary, error: ${error}, info: ${errorInfo}`);
    this.setState({ error, errorInfo });
  }

  render() {
    const { slackChannel, children } = this.props;
    const { error } = this.state;

    if (!error) {
      return children;
    }

    return (
      React.createElement(ErrorPanel, { title: "Something Went Wrong"  , error: error,}
        , React.createElement(SlackLink, { slackChannel: slackChannel,} )
      )
    );
  }
};
