import { basicIntegrations, defaultScmResolveUrl } from '../helpers';

import { readAwsS3IntegrationConfigs } from './config';

/**
 * Integrates with AWS S3 or compatible solutions.
 *
 * @public
 */
export class AwsS3Integration  {
  static factory = ({ config }) => {
    const configs = readAwsS3IntegrationConfigs(
      config.getOptionalConfigArray('integrations.awsS3') ?? [],
    );
    return basicIntegrations(
      configs.map(c => new AwsS3Integration(c)),
      i => i.config.host,
    );
  };

  get type() {
    return 'awsS3';
  }

  get title() {
    return this.integrationConfig.host;
  }

  get config() {
    return this.integrationConfig;
  }

  constructor(  integrationConfig) {;this.integrationConfig = integrationConfig;}

  resolveUrl(options



) {
    const resolved = defaultScmResolveUrl(options);
    return resolved;
  }

  resolveEditUrl(url) {
    // TODO: Implement edit URL for awsS3
    return url;
  }
}
