import React from 'react';
import { CircularProgress, Button, makeStyles } from '@material-ui/core';
import { Alert } from '@material-ui/lab';

import { TechDocsBuildLogs } from './TechDocsBuildLogs';
import { TechDocsNotFound } from './TechDocsNotFound';
import { useTechDocsReader } from './TechDocsReaderProvider';

const useStyles = makeStyles(theme => ({
  root: {
    marginBottom: theme.spacing(2),
  },
  message: {
    // `word-break: break-word` is deprecated, but gives legacy support to browsers not supporting `overflow-wrap` yet
    // https://developer.mozilla.org/en-US/docs/Web/CSS/word-break
    wordBreak: 'break-word',
    overflowWrap: 'anywhere',
  },
}));

export const TechDocsStateIndicator = () => {
  let StateAlert = null;
  const classes = useStyles();

  const {
    state,
    contentReload,
    contentErrorMessage,
    syncErrorMessage,
    buildLog,
  } = useTechDocsReader();

  if (state === 'INITIAL_BUILD') {
    StateAlert = (
      React.createElement(Alert, {
        classes: { root: classes.root },
        variant: "outlined",
        severity: "info",
        icon: React.createElement(CircularProgress, { size: "24px",} ),
        action: React.createElement(TechDocsBuildLogs, { buildLog: buildLog,} ),}
      , "Documentation is accessed for the first time and is being prepared. The subsequent loads are much faster."


      )
    );
  }

  if (state === 'CONTENT_STALE_REFRESHING') {
    StateAlert = (
      React.createElement(Alert, {
        variant: "outlined",
        severity: "info",
        icon: React.createElement(CircularProgress, { size: "24px",} ),
        action: React.createElement(TechDocsBuildLogs, { buildLog: buildLog,} ),
        classes: { root: classes.root },}
      , "A newer version of this documentation is being prepared and will be available shortly."


      )
    );
  }

  if (state === 'CONTENT_STALE_READY') {
    StateAlert = (
      React.createElement(Alert, {
        variant: "outlined",
        severity: "success",
        action: 
          React.createElement(Button, { color: "inherit", onClick: () => contentReload(),}, "Refresh"

          )
        ,
        classes: { root: classes.root },}
      , "A newer version of this documentation is now available, please refresh to view."


      )
    );
  }

  if (state === 'CONTENT_STALE_ERROR') {
    StateAlert = (
      React.createElement(Alert, {
        variant: "outlined",
        severity: "error",
        action: React.createElement(TechDocsBuildLogs, { buildLog: buildLog,} ),
        classes: { root: classes.root, message: classes.message },}
      , "Building a newer version of this documentation failed."
               , ' '
        , syncErrorMessage
      )
    );
  }

  if (state === 'CONTENT_NOT_FOUND') {
    StateAlert = (
      React.createElement(React.Fragment, null
        , syncErrorMessage && (
          React.createElement(Alert, {
            variant: "outlined",
            severity: "error",
            action: React.createElement(TechDocsBuildLogs, { buildLog: buildLog,} ),
            classes: { root: classes.root, message: classes.message },}
          , "Building a newer version of this documentation failed."
                   , ' '
            , syncErrorMessage
          )
        )
        , React.createElement(TechDocsNotFound, { errorMessage: contentErrorMessage,} )
      )
    );
  }

  return StateAlert;
};
