
















import { OAuth2 } from '../oauth2';


const DEFAULT_PROVIDER = {
  id: 'okta',
  title: 'Okta',
  icon: () => null,
};

const OKTA_OIDC_SCOPES = new Set([
  'openid',
  'profile',
  'email',
  'phone',
  'address',
  'groups',
  'offline_access',
]);

const OKTA_SCOPE_PREFIX = 'okta.';

/**
 * Implements the OAuth flow to Okta products.
 *
 * @public
 */
export default class OktaAuth {
  static create(options) {
    const {
      discoveryApi,
      environment = 'development',
      provider = DEFAULT_PROVIDER,
      oauthRequestApi,
      defaultScopes = ['openid', 'email', 'profile', 'offline_access'],
    } = options;

    return OAuth2.create({
      discoveryApi,
      oauthRequestApi,
      provider,
      environment,
      defaultScopes,
      scopeTransform(scopes) {
        return scopes.map(scope => {
          if (OKTA_OIDC_SCOPES.has(scope)) {
            return scope;
          }

          if (scope.startsWith(OKTA_SCOPE_PREFIX)) {
            return scope;
          }

          return `${OKTA_SCOPE_PREFIX}${scope}`;
        });
      },
    });
  }
}
