import React, { useMemo } from 'react';
import { makeStyles } from '@material-ui/core';

const useStyles = makeStyles(
  () => ({
    highlight: {},
  }),
  { name: 'BackstageHighlightedSearchResultText' },
);

/**
 * @public
 */






/**
 * @public
 */
export const HighlightedSearchResultText = ({
  text,
  preTag,
  postTag,
}) => {
  const classes = useStyles();
  const terms = useMemo(
    () => text.split(new RegExp(`(${preTag}.+?${postTag})`)),
    [postTag, preTag, text],
  );
  return (
    React.createElement(React.Fragment, null
      , terms.map((t, idx) =>
        t.includes(preTag) ? (
          React.createElement('mark', { className: classes.highlight, key: idx,}
            , t.replace(new RegExp(`${preTag}|${postTag}`, 'g'), '')
          )
        ) : (
          t
        ),
      )
    )
  );
};
