






















export class Tracker  {
  constructor(
      analyticsApi,
     context = {
      routeRef: 'unknown',
      pluginId: 'root',
      extension: 'App',
    },
  ) {;this.analyticsApi = analyticsApi;this.context = context;}

  setContext(context) {
    this.context = context;
  }

  captureEvent(
    action,
    subject,
    {
      value,
      attributes,
    } = {},
  ) {
    try {
      this.analyticsApi.captureEvent({
        action,
        subject,
        value,
        attributes,
        context: this.context,
      });
    } catch (e) {
      // eslint-disable-next-line no-console
      console.warn('Error during analytics event capture. %o', e);
    }
  }
}
