import React, { useContext, } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

import { VerticalStepperContext } from './SimpleStepper';



const useStyles = makeStyles(
  theme => ({
    root: {
      marginTop: theme.spacing(3),
      '& button': {
        marginRight: theme.spacing(1),
      },
    },
  }),
  { name: 'BackstageSimpleStepperFooter' },
);

















export const RestartBtn = ({ text, handleClick }) => (
  React.createElement(Button, { onClick: handleClick,}, text || 'Reset')
);

const NextBtn = ({
  text,
  handleClick,
  disabled,
  last,
  stepIndex,
}) => (
  React.createElement(Button, {
    variant: "contained",
    color: "primary",
    disabled: disabled,
    'data-testid': `nextButton-${stepIndex}`,
    onClick: handleClick,}
  
    , text || (last ? 'Finish' : 'Next')
  )
);

const BackBtn = ({ text, handleClick, disabled, stepIndex }) => (
  React.createElement(Button, {
    onClick: handleClick,
    'data-testid': `backButton-${stepIndex}`,
    disabled: disabled,}
  
    , text || 'Back'
  )
);






export const SimpleStepperFooter = ({
  actions = {},
  children,
}) => {
  const classes = useStyles();
  const {
    stepperLength,
    stepIndex,
    setStepIndex,
    stepHistory,
    setStepHistory,
    onStepChange,
  } = useContext(VerticalStepperContext);

  const onChange = (newIndex, callback) => {
    if (callback) {
      callback();
    }
    if (onStepChange) {
      onStepChange(stepIndex, newIndex);
    }

    setStepIndex(newIndex);
  };

  const handleNext = () => {
    const newIndex = actions.nextStep
      ? actions.nextStep(stepIndex, stepperLength - 1)
      : stepIndex + 1;
    onChange(newIndex, actions.onNext);
    setStepHistory([...stepHistory, newIndex]);
  };
  const handleBack = () => {
    stepHistory.pop();
    onChange(stepHistory[stepHistory.length - 1], actions.onBack);
    setStepHistory([...stepHistory]);
  };
  const handleRestart = () => {
    onChange(0, actions.onRestart);
    setStepHistory([0]);
  };

  return (
    React.createElement('div', { className: classes.root,}
      , [undefined, true].includes(actions.showBack) && stepIndex !== 0 && (
        React.createElement(BackBtn, {
          text: actions.backText,
          handleClick: handleBack,
          disabled: stepIndex === 0,
          stepIndex: stepIndex,}
        )
      )
      , [undefined, true].includes(actions.showNext) && (
        React.createElement(NextBtn, {
          text: actions.nextText,
          handleClick: handleNext,
          disabled: 
            (!!stepperLength && stepIndex >= stepperLength) ||
            (!!actions.canNext && !actions.canNext())
          ,
          stepIndex: stepIndex,}
        )
      )
      , actions.showRestart && stepIndex !== 0 && (
        React.createElement(RestartBtn, {
          text: actions.restartText,
          handleClick: handleRestart,
          stepIndex: stepIndex,}
        )
      )
      , children
    )
  );
};
