import React, { useEffect, useState } from 'react';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import { Select } from '../Select';




const useFilterStyles = makeStyles(
  theme => ({
    root: {
      height: '100%',
      width: '315px',
      display: 'flex',
      flexDirection: 'column',
      marginRight: theme.spacing(3),
    },
    value: {
      fontWeight: 'bold',
      fontSize: 18,
    },
    header: {
      display: 'flex',
      alignItems: 'center',
      height: '60px',
      justifyContent: 'space-between',
      borderBottom: `1px solid ${theme.palette.grey[500]}`,
    },
    filters: {
      display: 'flex',
      flexDirection: 'column',
      '& > *': {
        marginTop: theme.spacing(2),
      },
    },
  }),
  { name: 'BackstageTableFilters' },
);


















export const Filters = (props) => {
  const classes = useFilterStyles();

  const { onChangeFilters } = props;

  const [selectedFilters, setSelectedFilters] = useState({
    ...props.selectedFilters,
  });
  const [reset, triggerReset] = useState(false);

  // Trigger re-rendering
  const handleClick = () => {
    setSelectedFilters({});
    triggerReset(el => !el);
  };

  useEffect(() => {
    onChangeFilters(selectedFilters);
  }, [selectedFilters, onChangeFilters]);

  // As material table doesn't provide a way to add a column filter tab we will make our own filter logic
  return (
    React.createElement('div', { className: classes.root,}
      , React.createElement('div', { className: classes.header,}
        , React.createElement('div', { className: classes.value,}, "Filters")
        , React.createElement(Button, { color: "primary", onClick: handleClick,}, "Clear all"

        )
      )
      , React.createElement('div', { className: classes.filters,}
        , props.filters?.length &&
          props.filters.map(filter => (
            React.createElement(Select, {
              triggerReset: reset,
              key: filter.element.label,
              ...(filter.element ),
              selected: selectedFilters[filter.element.label],
              onChange: el =>
                setSelectedFilters({
                  ...selectedFilters,
                  [filter.element.label]: el ,
                })
              ,}
            )
          ))
      )
    )
  );
};
