















import { makeStyles, darken, lighten } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import ErrorOutline from '@material-ui/icons/ErrorOutline';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import React from 'react';

const getWarningTextColor = (
  severity,
  theme,
) => {
  const getColor = theme.palette.type === 'light' ? darken : lighten;
  return getColor(theme.palette[severity].light, 0.6);
};

const getWarningBackgroundColor = (
  severity,
  theme,
) => {
  const getBackgroundColor = theme.palette.type === 'light' ? lighten : darken;
  return getBackgroundColor(theme.palette[severity].light, 0.9);
};

const useErrorOutlineStyles = makeStyles(theme => ({
  root: {
    marginRight: theme.spacing(1),
    fill: ({ severity }) =>
      getWarningTextColor(
        severity ,
        theme,
      ),
  },
}));

const ErrorOutlineStyled = ({ severity }) => {
  const classes = useErrorOutlineStyles({ severity });
  return React.createElement(ErrorOutline, { classes: classes,} );
};
const ExpandMoreIconStyled = ({ severity }) => {
  const classes = useErrorOutlineStyles({ severity });
  return React.createElement(ExpandMoreIcon, { classes: classes,} );
};








const useStyles = makeStyles(
  theme => ({
    panel: {
      backgroundColor: ({ severity }) =>
        getWarningBackgroundColor(
          severity ,
          theme,
        ),
      color: ({ severity }) =>
        getWarningTextColor(
          severity ,
          theme,
        ),
      verticalAlign: 'middle',
    },
    summary: {
      display: 'flex',
      flexDirection: 'row',
    },
    summaryText: {
      color: ({ severity }) =>
        getWarningTextColor(
          severity ,
          theme,
        ),
      fontWeight: 'bold',
    },
    message: {
      width: '100%',
      display: 'block',
      color: ({ severity }) =>
        getWarningTextColor(
          severity ,
          theme,
        ),
      backgroundColor: ({ severity }) =>
        getWarningBackgroundColor(
          severity ,
          theme,
        ),
    },
    details: {
      width: '100%',
      display: 'block',
      color: theme.palette.textContrast,
      backgroundColor: theme.palette.background.default,
      border: `1px solid ${theme.palette.border}`,
      padding: theme.spacing(2.0),
      fontFamily: 'sans-serif',
    },
  }),
  { name: 'BackstageWarningPanel' },
);









const capitalize = (s) => {
  return s.charAt(0).toLocaleUpperCase('en-US') + s.slice(1);
};

/**
 * WarningPanel. Show a user friendly error message to a user similar to
 * ErrorPanel except that the warning panel only shows the warning message to
 * the user.
 *
 * @param severity - Ability to change the severity of the alert. Default value
 *        "warning"
 * @param title - A title for the warning. If not supplied, "Warning" will be
 *        used.
 * @param message - Optional more detailed user-friendly message elaborating on
 *        the cause of the error.
 * @param children - Objects to provide context, such as a stack trace or detailed
 *        error reporting. Will be available inside an unfolded accordion.
 */
export function WarningPanel(props) {
  const {
    severity = 'warning',
    title,
    message,
    children,
    defaultExpanded,
  } = props;
  const classes = useStyles({ severity });

  // If no severity or title provided, the heading will read simply "Warning"
  const subTitle = capitalize(severity) + (title ? `: ${title}` : '');

  return (
    React.createElement(Accordion, {
      defaultExpanded: defaultExpanded ?? false,
      className: classes.panel,
      role: "alert",}
    
      , React.createElement(AccordionSummary, {
        expandIcon: React.createElement(ExpandMoreIconStyled, { severity: severity,} ),
        className: classes.summary,}
      
        , React.createElement(ErrorOutlineStyled, { severity: severity,} )
        , React.createElement(Typography, { className: classes.summaryText, variant: "subtitle1",}
          , subTitle
        )
      )
      , (message || children) && (
        React.createElement(AccordionDetails, null
          , React.createElement(Grid, { container: true,}
            , message && (
              React.createElement(Grid, { item: true, xs: 12,}
                , React.createElement(Typography, { className: classes.message, variant: "body1",}
                  , message
                )
              )
            )
            , children && (
              React.createElement(Grid, { item: true, xs: 12, className: classes.details,}
                , children
              )
            )
          )
        )
      )
    )
  );
}
