import { makeStyles, } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import React, { useState } from 'react';
import { isError } from '@backstage/errors';




const useItemStyles = makeStyles(
  theme => ({
    root: {
      paddingLeft: theme.spacing(3),
    },
  }),
  { name: 'BackstageLoginRequestListItem' },
);







const LoginRequestListItem = ({ request, busy, setBusy }) => {
  const classes = useItemStyles();
  const [error, setError] = useState();

  const handleContinue = async () => {
    setBusy(true);
    try {
      await request.trigger();
    } catch (e) {
      setError(isError(e) ? e.message : 'An unspecified error occurred');
    } finally {
      setBusy(false);
    }
  };

  const IconComponent = request.provider.icon;

  return (
    React.createElement(ListItem, { disabled: busy, classes: { root: classes.root },}
      , React.createElement(ListItemAvatar, null
        , React.createElement(IconComponent, { fontSize: "large",} )
      )
      , React.createElement(ListItemText, {
        primary: request.provider.title,
        secondary: error && React.createElement(Typography, { color: "error",}, error),}
      )
      , React.createElement(Button, { color: "primary", variant: "contained", onClick: handleContinue,}, "Log in"

      )
    )
  );
};

export default LoginRequestListItem;
