import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import Divider from '@material-ui/core/Divider';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Tab, { } from '@material-ui/core/Tab';
import Tabs from '@material-ui/core/Tabs';
import React, {



  useState,
} from 'react';
import { BottomLink, } from '../BottomLink';
import { ErrorBoundary, } from '../ErrorBoundary';



const useTabsStyles = makeStyles(
  theme => ({
    root: {
      padding: theme.spacing(0, 2, 0, 2.5),
      minHeight: theme.spacing(3),
    },
    indicator: {
      backgroundColor: theme.palette.info.main,
      height: theme.spacing(0.3),
    },
  }),
  { name: 'BackstageTabbedCard' },
);

/** @public */


const BoldHeader = withStyles(
  theme => ({
    root: { padding: theme.spacing(2, 2, 2, 2.5), display: 'inline-block' },
    title: { fontWeight: 700 },
    subheader: { paddingTop: theme.spacing(1) },
  }),
  { name: 'BackstageTabbedCardBoldHeader' },
)(CardHeader);












export function TabbedCard(props) {
  const {
    slackChannel,
    errorBoundaryProps,
    children,
    title,
    deepLink,
    value,
    onChange,
  } = props;
  const tabsClasses = useTabsStyles();
  const [selectedIndex, selectIndex] = useState(0);

  const handleChange = onChange
    ? onChange
    : (_ev, newSelectedIndex) => selectIndex(newSelectedIndex);

  let selectedTabContent;
  if (!value) {
    React.Children.map(children, (child, index) => {
      if (React.isValidElement(child) && index === selectedIndex) {
        selectedTabContent = child?.props.children;
      }
    });
  } else {
    React.Children.map(children, child => {
      if (React.isValidElement(child) && child?.props.value === value) {
        selectedTabContent = child?.props.children;
      }
    });
  }

  const errProps =
    errorBoundaryProps || (slackChannel ? { slackChannel } : {});

  return (
    React.createElement(Card, null
      , React.createElement(ErrorBoundary, { ...errProps,}
        , title && React.createElement(BoldHeader, { title: title,} )
        , React.createElement(Tabs, {
          selectionFollowsFocus: true,
          classes: tabsClasses,
          value: value || selectedIndex,
          onChange: handleChange,}
        
          , children
        )
        , React.createElement(Divider, null )
        , React.createElement(CardContent, null, selectedTabContent)
        , deepLink && React.createElement(BottomLink, { ...deepLink,} )
      )
    )
  );
}

/** @public */


const useCardTabStyles = makeStyles(
  theme => ({
    root: {
      minWidth: theme.spacing(6),
      minHeight: theme.spacing(3),
      margin: theme.spacing(0, 2, 0, 0),
      padding: theme.spacing(0.5, 0, 0.5, 0),
      textTransform: 'none',
      '&:hover': {
        opacity: 1,
        backgroundColor: 'transparent',
        color: theme.palette.text.primary,
      },
    },
    selected: {
      fontWeight: 'bold',
    },
  }),
  { name: 'BackstageCardTab' },
);





/**
 * Card tab component used in {@link TabbedCard}
 *
 * @public
 *
 */
export function CardTab(props) {
  const { children, ...restProps } = props;
  const classes = useCardTabStyles();

  return React.createElement(Tab, { disableRipple: true, classes: classes, ...restProps,} );
}
