import { makeStyles } from '@material-ui/core/styles';
import React, { } from 'react';

import { InfoCard, } from '../../layout/InfoCard';
import { Gauge, } from './Gauge';

















const useStyles = makeStyles(
  {
    root: {
      height: '100%',
      width: 250,
    },
  },
  { name: 'BackstageGaugeCard' },
);

/**
 * {@link Gauge} with header, subheader and footer
 *
 * @public
 *
 */
export function GaugeCard(props) {
  const classes = useStyles(props);
  const {
    title,
    subheader,
    progress,
    inverse,
    deepLink,
    description,
    icon,
    variant,
    getColor,
  } = props;

  const gaugeProps = {
    inverse,
    description,
    getColor,
    value: progress,
  };

  return (
    React.createElement('div', { className: classes.root,}
      , React.createElement(InfoCard, {
        title: title,
        subheader: subheader,
        deepLink: deepLink,
        variant: variant,
        icon: icon,}
      
        , React.createElement(Gauge, { ...gaugeProps,} )
      )
    )
  );
}
