import React from 'react';

import { makeStyles } from '@material-ui/core/styles';



const useSubvalueCellStyles = makeStyles(
  theme => ({
    value: {
      marginBottom: '6px',
    },
    subvalue: {
      color: theme.palette.textSubtle,
      fontWeight: 'normal',
    },
  }),
  { name: 'BackstageSubvalueCell' },
);






export function SubvalueCell(props) {
  const { value, subvalue } = props;
  const classes = useSubvalueCellStyles();

  return (
    React.createElement(React.Fragment, null
      , React.createElement('div', { className: classes.value,}, value)
      , React.createElement('div', { className: classes.subvalue,}, subvalue)
    )
  );
}
