

















import { PublishSubject } from '../../../lib/subjects';

/**
 * Base implementation for the AlertApi that simply forwards alerts to consumers.
 *
 * @public
 */
export class AlertApiForwarder  {
    subject = new PublishSubject();

  post(alert) {
    this.subject.next(alert);
  }

  alert$() {
    return this.subject;
  }
}
