import {

  ANNOTATION_LOCATION,
  ANNOTATION_ORIGIN_LOCATION,
  stringifyEntityRef,
} from '@backstage/catalog-model';
import { Progress, ResponseErrorPanel } from '@backstage/core-components';
import { useApi } from '@backstage/core-plugin-api';
import {
  DialogContentText,
  List,
  ListItem,
  ListItemIcon,
  makeStyles,
} from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import React from 'react';
import useAsync from 'react-use/lib/useAsync';
import { catalogApiRef } from '../../../api';
import { EntityRefLink } from '../../EntityRefLink';
import { KeyValueListItem, ListItemText } from './common';
import { EntityKindIcon } from './EntityKindIcon';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'column',
  },
});

function useColocated(entity)





 {
  const catalogApi = useApi(catalogApiRef);
  const currentEntityRef = stringifyEntityRef(entity);
  const location = entity.metadata.annotations?.[ANNOTATION_LOCATION];
  const origin = entity.metadata.annotations?.[ANNOTATION_ORIGIN_LOCATION];

  const { loading, error, value } = useAsync(async () => {
    if (!location && !origin) {
      return [];
    }
    const response = await catalogApi.getEntities({
      filter: [
        ...(location
          ? [{ [`metadata.annotations.${ANNOTATION_LOCATION}`]: location }]
          : []),
        ...(origin
          ? [{ [`metadata.annotations.${ANNOTATION_ORIGIN_LOCATION}`]: origin }]
          : []),
      ],
    });
    return response.items;
  }, [location, origin]);

  return {
    loading,
    error,
    location,
    originLocation: origin,
    colocatedEntities: value?.filter(
      colocated => stringifyEntityRef(colocated) !== currentEntityRef,
    ),
  };
}

function EntityList(props) {
  return (
    React.createElement(List, { dense: true,}
      , props.header && React.createElement(KeyValueListItem, { key: "header", entry: props.header,} )
      , props.entities.map(entity => (
        React.createElement(ListItem, { key: stringifyEntityRef(entity),}
          , React.createElement(ListItemIcon, null
            , React.createElement(EntityKindIcon, { kind: entity.kind,} )
          )
          , React.createElement(ListItemText, { primary: React.createElement(EntityRefLink, { entityRef: entity,} ),} )
        )
      ))
    )
  );
}

function Contents(props) {
  const { entity } = props;

  const { loading, error, location, originLocation, colocatedEntities } =
    useColocated(entity);
  if (loading) {
    return React.createElement(Progress, null );
  } else if (error) {
    return React.createElement(ResponseErrorPanel, { error: error,} );
  }

  if (!location && !originLocation) {
    return (
      React.createElement(Alert, { severity: "warning",}, "Entity had no location information."    )
    );
  } else if (!colocatedEntities?.length) {
    return (
      React.createElement(Alert, { severity: "info",}, "There were no other entities on this location."

      )
    );
  }

  if (location === originLocation) {
    return React.createElement(EntityList, { entities: colocatedEntities,} );
  }

  const atLocation = colocatedEntities.filter(
    e => e.metadata.annotations?.[ANNOTATION_LOCATION] === location,
  );
  const atOrigin = colocatedEntities.filter(
    e =>
      e.metadata.annotations?.[ANNOTATION_ORIGIN_LOCATION] === originLocation,
  );

  return (
    React.createElement(React.Fragment, null
      , atLocation.length > 0 && (
        React.createElement(EntityList, {
          entities: atLocation,
          header: ['At the same location', location],}
        )
      )
      , atOrigin.length > 0 && (
        React.createElement(EntityList, {
          entities: atOrigin,
          header: ['At the same origin', originLocation],}
        )
      )
    )
  );
}

export function ColocatedPage(props) {
  const classes = useStyles();
  return (
    React.createElement(React.Fragment, null
      , React.createElement(DialogContentText, { variant: "h2",}, "Colocated")
      , React.createElement(DialogContentText, null, "These are the entities that are colocated with this entity - as in, they originated from the same data source (e.g. came from the same YAML file), or from the same origin (e.g. the originally registered URL)."



      )
      , React.createElement('div', { className: classes.root,}
        , React.createElement(Contents, { entity: props.entity,} )
      )
    )
  );
}
