import React from 'react';
import {
  withStyles,
  createStyles,


} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';



const tableTitleCellStyles = (theme) =>
  createStyles({
    root: {
      fontWeight: 'bolder',
      whiteSpace: 'nowrap',
      paddingRight: theme.spacing(4),
      border: '0',
      verticalAlign: 'top',
    },
  });



const tableContentCellStyles = {
  root: {
    border: '0',
    verticalAlign: 'top',
  },
};



const listStyles = (theme) =>
  createStyles({
    root: {
      listStyle: 'none',
      margin: theme.spacing(0, 0, -1, 0),
      padding: '0',
    },
  });



const listItemStyles = (theme) =>
  createStyles({
    root: {
      padding: theme.spacing(0, 0, 1, 0),
    },
    random: {},
  });

const TitleCell = withStyles(tableTitleCellStyles, {
  name: 'BackstageMetadataTableTitleCell',
})(TableCell);
const ContentCell = withStyles(tableContentCellStyles, {
  name: 'BackstageMetadataTableCell',
})(TableCell);

export const MetadataTable = ({
  dense,
  children,
}


) => (
  React.createElement(Table, { size: dense ? 'small' : 'medium',}
    , React.createElement(TableBody, null, children)
  )
);

export const MetadataTableItem = ({
  title,
  children,
  ...rest
}


) => (
  React.createElement(TableRow, null
    , title && React.createElement(TitleCell, null, title)
    , React.createElement(ContentCell, { colSpan: title ? 1 : 2, ...rest,}
      , children
    )
  )
);





export const MetadataList = withStyles(listStyles, {
  name: 'BackstageMetadataTableList',
})(({ classes, children }) => (
  React.createElement('ul', { className: classes.root,}, children)
));

export const MetadataListItem = withStyles(listItemStyles, {
  name: 'BackstageMetadataTableListItem',
})(({ classes, children }) => (
  React.createElement('li', { className: classes.root,}, children)
));
