import { configApiRef, useApi } from '@backstage/core-plugin-api';

import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';
import React, { } from 'react';
import { Helmet } from 'react-helmet';
import { Link } from '../../components/Link';
import { Breadcrumbs } from '../Breadcrumbs';

/** @public */











const useStyles = makeStyles(
  theme => ({
    header: {
      gridArea: 'pageHeader',
      padding: theme.spacing(3),
      width: '100%',
      boxShadow: theme.shadows[4],
      position: 'relative',
      zIndex: 100,
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'center',
      backgroundImage: theme.page.backgroundImage,
      backgroundPosition: 'center',
      backgroundSize: 'cover',
      [theme.breakpoints.down('sm')]: {
        flexWrap: 'wrap',
      },
    },
    leftItemsBox: {
      maxWidth: '100%',
      flexGrow: 1,
    },
    rightItemsBox: {
      width: 'auto',
    },
    title: {
      color: theme.palette.bursts.fontColor,
      wordBreak: 'break-word',
      fontSize: theme.typography.h3.fontSize,
      marginBottom: 0,
    },
    subtitle: {
      color: theme.palette.bursts.fontColor,
      opacity: 0.8,
      display: 'inline-block', // prevents margin collapse of adjacent siblings
      marginTop: theme.spacing(1),
      maxWidth: '75ch',
    },
    type: {
      textTransform: 'uppercase',
      fontSize: 11,
      opacity: 0.8,
      marginBottom: theme.spacing(1),
      color: theme.palette.bursts.fontColor,
    },
    breadcrumb: {
      color: theme.palette.bursts.fontColor,
    },
    breadcrumbType: {
      fontSize: 'inherit',
      opacity: 0.7,
      marginRight: -theme.spacing(0.3),
      marginBottom: theme.spacing(0.3),
    },
    breadcrumbTitle: {
      fontSize: 'inherit',
      marginLeft: -theme.spacing(0.3),
      marginBottom: theme.spacing(0.3),
    },
  }),
  { name: 'BackstageHeader' },
);
































const TypeFragment = ({
  type,
  typeLink,
  classes,
  pageTitle,
}) => {
  if (!type) {
    return null;
  }

  if (!typeLink) {
    return React.createElement(Typography, { className: classes.type,}, type);
  }

  return (
    React.createElement(Breadcrumbs, { className: classes.breadcrumb,}
      , React.createElement(Link, { to: typeLink,}, type)
      , React.createElement(Typography, null, pageTitle)
    )
  );
};

const TitleFragment = ({ pageTitle, classes, tooltip }) => {
  const FinalTitle = (
    React.createElement(Typography, { className: classes.title, variant: "h1",}
      , pageTitle
    )
  );

  if (!tooltip) {
    return FinalTitle;
  }

  return (
    React.createElement(Tooltip, { title: tooltip, placement: "top-start",}
      , FinalTitle
    )
  );
};

const SubtitleFragment = ({ classes, subtitle }) => {
  if (!subtitle) {
    return null;
  }

  if (typeof subtitle !== 'string') {
    return React.createElement(React.Fragment, null, subtitle);
  }

  return (
    React.createElement(Typography, {
      className: classes.subtitle,
      variant: "subtitle2",
      component: "span",}
    
      , subtitle
    )
  );
};
/**
 * Backstage main header with abstract color background in multiple variants
 *
 * @public
 *
 */
export function Header(props) {
  const {
    children,
    pageTitleOverride,
    style,
    subtitle,
    title,
    tooltip,
    type,
    typeLink,
  } = props;
  const classes = useStyles();
  const configApi = useApi(configApiRef);
  const appTitle = configApi.getOptionalString('app.title') || 'Backstage';
  const documentTitle = pageTitleOverride || title;
  const pageTitle = title || pageTitleOverride;
  const titleTemplate = `${documentTitle} | %s | ${appTitle}`;
  const defaultTitle = `${documentTitle} | ${appTitle}`;

  return (
    React.createElement(React.Fragment, null
      , React.createElement(Helmet, { titleTemplate: titleTemplate, defaultTitle: defaultTitle,} )
      , React.createElement('header', { style: style, className: classes.header,}
        , React.createElement(Box, { className: classes.leftItemsBox,}
          , React.createElement(TypeFragment, {
            classes: classes,
            type: type,
            typeLink: typeLink,
            pageTitle: pageTitle,}
          )
          , React.createElement(TitleFragment, {
            classes: classes,
            pageTitle: pageTitle,
            tooltip: tooltip,}
          )
          , React.createElement(SubtitleFragment, { classes: classes, subtitle: subtitle,} )
        )
        , React.createElement(Grid, { container: true, className: classes.rightItemsBox, spacing: 4,}
          , children
        )
      )
    )
  );
}
