
















import {
  Box,
  Checkbox,
  FormControlLabel,
  makeStyles,
  TextField,
  Typography,
} from '@material-ui/core';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Autocomplete } from '@material-ui/lab';
import React, { useEffect, useMemo, useState } from 'react';
import { useEntityList } from '../../hooks/useEntityListProvider';
import { EntityLifecycleFilter } from '../../filters';

/** @public */


const useStyles = makeStyles(
  {
    input: {},
  },
  {
    name: 'CatalogReactEntityLifecyclePicker',
  },
);

const icon = React.createElement(CheckBoxOutlineBlankIcon, { fontSize: "small",} );
const checkedIcon = React.createElement(CheckBoxIcon, { fontSize: "small",} );

/** @public */
export const EntityLifecyclePicker = () => {
  const classes = useStyles();
  const {
    updateFilters,
    backendEntities,
    filters,
    queryParameters: { lifecycles: lifecyclesParameter },
  } = useEntityList();

  const queryParamLifecycles = useMemo(
    () => [lifecyclesParameter].flat().filter(Boolean) ,
    [lifecyclesParameter],
  );

  const [selectedLifecycles, setSelectedLifecycles] = useState(
    queryParamLifecycles.length
      ? queryParamLifecycles
      : filters.lifecycles?.values ?? [],
  );

  // Set selected lifecycles on query parameter updates; this happens at initial page load and from
  // external updates to the page location.
  useEffect(() => {
    if (queryParamLifecycles.length) {
      setSelectedLifecycles(queryParamLifecycles);
    }
  }, [queryParamLifecycles]);

  useEffect(() => {
    updateFilters({
      lifecycles: selectedLifecycles.length
        ? new EntityLifecycleFilter(selectedLifecycles)
        : undefined,
    });
  }, [selectedLifecycles, updateFilters]);

  const availableLifecycles = useMemo(
    () =>
      [
        ...new Set(
          backendEntities
            .map((e) => e.spec?.lifecycle)
            .filter(Boolean) ,
        ),
      ].sort(),
    [backendEntities],
  );

  if (!availableLifecycles.length) return null;

  return (
    React.createElement(Box, { pb: 1, pt: 1,}
      , React.createElement(Typography, { variant: "button", component: "label",}, "Lifecycle"

        , React.createElement(Autocomplete, {
          multiple: true,
          options: availableLifecycles,
          value: selectedLifecycles,
          onChange: (_, value) =>
            setSelectedLifecycles(value)
          ,
          renderOption: (option, { selected }) => (
            React.createElement(FormControlLabel, {
              control: 
                React.createElement(Checkbox, {
                  icon: icon,
                  checkedIcon: checkedIcon,
                  checked: selected,}
                )
              ,
              label: option,}
            )
          ),
          size: "small",
          popupIcon: React.createElement(ExpandMoreIcon, { 'data-testid': "lifecycle-picker-expand",} ),
          renderInput: params => (
            React.createElement(TextField, {
              ...params,
              className: classes.input,
              variant: "outlined",}
            )
          ),}
        )
      )
    )
  );
};
