import {

  DEFAULT_NAMESPACE,
  RELATION_OWNED_BY,
} from '@backstage/catalog-model';
import {
  Content,
  Header,
  HeaderLabel,
  Link,
  Page,
  Progress,
  RoutedTabs,
  WarningPanel,
} from '@backstage/core-components';
import {
  attachComponentData,

  useElementFilter,
  useRouteRefParams,
} from '@backstage/core-plugin-api';
import {
  EntityRefLinks,
  entityRouteRef,
  FavoriteEntity,
  getEntityRelations,
  InspectEntityDialog,
  UnregisterEntityDialog,
  useAsyncEntity,
} from '@backstage/plugin-catalog-react';
import { Box, } from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router';
import { EntityContextMenu } from '../EntityContextMenu/EntityContextMenu';

/** @public */








const dataKey = 'plugin.catalog.entityLayoutRoute';

const Route = () => null;
attachComponentData(Route, dataKey, true);
attachComponentData(Route, 'core.gatherMountPoints', true); // This causes all mount points that are discovered within this route to use the path of the route itself

function EntityLayoutTitle(props


) {
  const { entity, title } = props;
  return (
    React.createElement(Box, { display: "inline-flex", alignItems: "center", height: "1em", maxWidth: "100%",}
      , React.createElement(Box, {
        component: "span",
        textOverflow: "ellipsis",
        whiteSpace: "nowrap",
        overflow: "hidden",}
      
        , title
      )
      , entity && React.createElement(FavoriteEntity, { entity: entity,} )
    )
  );
}

function headerProps(
  paramKind,
  paramNamespace,
  paramName,
  entity,
) {
  const kind = paramKind ?? entity?.kind ?? '';
  const namespace = paramNamespace ?? entity?.metadata.namespace ?? '';
  const name =
    entity?.metadata.title ?? paramName ?? entity?.metadata.name ?? '';
  return {
    headerTitle: `${name}${
      namespace && namespace !== DEFAULT_NAMESPACE ? ` in ${namespace}` : ''
    }`,
    headerType: (() => {
      let t = kind.toLocaleLowerCase('en-US');
      if (entity && entity.spec && 'type' in entity.spec) {
        t += '  ';
        t += (entity.spec ).type.toLocaleLowerCase('en-US');
      }
      return t;
    })(),
  };
}

function EntityLabels(props) {
  const { entity } = props;
  const ownedByRelations = getEntityRelations(entity, RELATION_OWNED_BY);
  return (
    React.createElement(React.Fragment, null
      , ownedByRelations.length > 0 && (
        React.createElement(HeaderLabel, {
          label: "Owner",
          value: 
            React.createElement(EntityRefLinks, {
              entityRefs: ownedByRelations,
              defaultKind: "Group",
              color: "inherit",}
            )
          ,}
        )
      )
      , entity.spec?.lifecycle && (
        React.createElement(HeaderLabel, { label: "Lifecycle", value: entity.spec.lifecycle,} )
      )
    )
  );
}

// NOTE(freben): Intentionally not exported at this point, since it's part of
// the unstable extra context menu items concept below



















/**
 * EntityLayout is a compound component, which allows you to define a layout for
 * entities using a sub-navigation mechanism.
 *
 * Consists of two parts: EntityLayout and EntityLayout.Route
 *
 * @example
 * ```jsx
 * <EntityLayout>
 *   <EntityLayout.Route path="/example" title="Example tab">
 *     <div>This is rendered under /example/anything-here route</div>
 *   </EntityLayout.Route>
 * </EntityLayout>
 * ```
 *
 * @public
 */
export const EntityLayout = (props) => {
  const {
    UNSTABLE_extraContextMenuItems,
    UNSTABLE_contextMenuOptions,
    children,
    NotFoundComponent,
  } = props;
  const { kind, namespace, name } = useRouteRefParams(entityRouteRef);
  const { entity, loading, error } = useAsyncEntity();
  const location = useLocation();
  const routes = useElementFilter(
    children,
    elements =>
      elements
        .selectByComponentData({
          key: dataKey,
          withStrictError:
            'Child of EntityLayout must be an EntityLayout.Route',
        })
        .getElements() // all nodes, element data, maintain structure or not?
        .flatMap(({ props: elementProps }) => {
          if (!entity) {
            return [];
          } else if (elementProps.if && !elementProps.if(entity)) {
            return [];
          }

          return [
            {
              path: elementProps.path,
              title: elementProps.title,
              children: elementProps.children,
              tabProps: elementProps.tabProps,
            },
          ];
        }),
    [entity],
  );

  const { headerTitle, headerType } = headerProps(
    kind,
    namespace,
    name,
    entity,
  );

  const [confirmationDialogOpen, setConfirmationDialogOpen] = useState(false);
  const [inspectionDialogOpen, setInspectionDialogOpen] = useState(false);
  const navigate = useNavigate();
  const cleanUpAfterRemoval = async () => {
    setConfirmationDialogOpen(false);
    setInspectionDialogOpen(false);
    navigate('/');
  };

  // Make sure to close the dialog if the user clicks links in it that navigate
  // to another entity.
  useEffect(() => {
    setConfirmationDialogOpen(false);
    setInspectionDialogOpen(false);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.pathname]);

  return (
    React.createElement(Page, { themeId: entity?.spec?.type?.toString() ?? 'home',}
      , React.createElement(Header, {
        title: React.createElement(EntityLayoutTitle, { title: headerTitle, entity: entity,} ),
        pageTitleOverride: headerTitle,
        type: headerType,}
      
        , entity && (
          React.createElement(React.Fragment, null
            , React.createElement(EntityLabels, { entity: entity,} )
            , React.createElement(EntityContextMenu, {
              UNSTABLE_extraContextMenuItems: UNSTABLE_extraContextMenuItems,
              UNSTABLE_contextMenuOptions: UNSTABLE_contextMenuOptions,
              onUnregisterEntity: () => setConfirmationDialogOpen(true),
              onInspectEntity: () => setInspectionDialogOpen(true),}
            )
          )
        )
      )

      , loading && React.createElement(Progress, null )

      , entity && React.createElement(RoutedTabs, { routes: routes,} )

      , error && (
        React.createElement(Content, null
          , React.createElement(Alert, { severity: "error",}, error.toString())
        )
      )

      , !loading && !error && !entity && (
        React.createElement(Content, null
          , NotFoundComponent ? (
            NotFoundComponent
          ) : (
            React.createElement(WarningPanel, { title: "Entity not found"  ,}, "There is no "
                 , kind, " with the requested"   , ' '
              , React.createElement(Link, { to: "https://backstage.io/docs/features/software-catalog/references",}, "kind, namespace, and name"

              ), "."

            )
          )
        )
      )

      , React.createElement(UnregisterEntityDialog, {
        open: confirmationDialogOpen,
        entity: entity,
        onConfirm: cleanUpAfterRemoval,
        onClose: () => setConfirmationDialogOpen(false),}
      )
      , React.createElement(InspectEntityDialog, {
        open: inspectionDialogOpen,
        entity: entity,
        onClose: () => setInspectionDialogOpen(false),}
      )
    )
  );
};

EntityLayout.Route = Route;
