
















import {
  SearchContextProvider,
  useSearch,
} from '@backstage/plugin-search-react';
import {
  makeStyles,
  CircularProgress,
  IconButton,
  InputAdornment,
  TextField,
} from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';
import Autocomplete from '@material-ui/lab/Autocomplete';
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router';
import useDebounce from 'react-use/lib/useDebounce';
import { TechDocsSearchResultListItem } from './TechDocsSearchResultListItem';

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
});

/**
 * Props for {@link TechDocsSearch}
 *
 * @public
 */



















const TechDocsSearchBar = (props) => {
  const { entityId, debounceTime = 150 } = props;
  const [open, setOpen] = useState(false);
  const navigate = useNavigate();
  const {
    term,
    setTerm,
    setFilters,
    result: { loading, value: searchVal },
  } = useSearch();
  const classes = useStyles();
  const [options, setOptions] = useState([]);
  useEffect(() => {
    let mounted = true;

    if (mounted && searchVal) {
      // TODO: Change this into getting only subset of search results from the BE in the first place
      // once pagination is implemented for search engines
      // See: https://github.com/backstage/backstage/issues/6062
      const searchResults = searchVal.results.slice(0, 10);
      setOptions(searchResults);
    }
    return () => {
      mounted = false;
    };
  }, [loading, searchVal]);

  const [value, setValue] = useState(term);

  useDebounce(() => setTerm(value), debounceTime, [value]);

  // Update the filter context when the entityId changes, e.g. when the search
  // bar continues to be rendered, navigating between different TechDocs sites.
  const { kind, name, namespace } = entityId;
  useEffect(() => {
    setFilters(prevFilters => {
      return {
        ...prevFilters,
        kind,
        namespace,
        name,
      };
    });
  }, [kind, namespace, name, setFilters]);

  const handleQuery = (e) => {
    if (!open) {
      setOpen(true);
    }
    setValue(e.target.value);
  };

  const handleSelection = (_, selection) => {
    if (selection?.document) {
      const { location } = selection.document;
      navigate(location);
    }
  };

  return (
    React.createElement(Autocomplete, {
      classes: { root: classes.root },
      'data-testid': "techdocs-search-bar",
      size: "small",
      open: open,
      getOptionLabel: () => '',
      filterOptions: x => {
        return x; // This is needed to get renderOption to be called after options change. Bug in material-ui?
      },
      onClose: () => {
        setOpen(false);
      },
      onFocus: () => {
        setOpen(true);
      },
      onChange: handleSelection,
      blurOnSelect: true,
      noOptionsText: "No results found"  ,
      value: null,
      options: options,
      renderOption: ({ document, highlight }) => (
        React.createElement(TechDocsSearchResultListItem, {
          result: document,
          lineClamp: 3,
          asListItem: false,
          asLink: false,
          title: document.title,
          highlight: highlight,}
        )
      ),
      loading: loading,
      renderInput: params => (
        React.createElement(TextField, {
          ...params,
          'data-testid': "techdocs-search-bar-input",
          variant: "outlined",
          fullWidth: true,
          placeholder: `Search ${entityId.name} docs`,
          value: value,
          onChange: handleQuery,
          InputProps: {
            ...params.InputProps,
            startAdornment: (
              React.createElement(InputAdornment, { position: "start",}
                , React.createElement(IconButton, { 'aria-label': "Query", disabled: true,}
                  , React.createElement(SearchIcon, null )
                )
              )
            ),
            endAdornment: (
              React.createElement(React.Fragment, null
                , loading ? (
                  React.createElement(CircularProgress, { color: "inherit", size: 20,} )
                ) : null
                , params.InputProps.endAdornment
              )
            ),
          },}
        )
      ),}
    )
  );
};

/**
 * Component used to render search bar on TechDocs page, scoped to
 *
 * @public
 */
export const TechDocsSearch = (props) => {
  const initialState = {
    term: '',
    types: ['techdocs'],
    pageCursor: '',
    filters: props.entityId,
  };
  return (
    React.createElement(SearchContextProvider, { initialState: initialState,}
      , React.createElement(TechDocsSearchBar, { ...props,} )
    )
  );
};
